.navbar{
    display: flex;
    flex-wrap: wrap;
    padding: 0;
    @include rwd($rwd-md){
        padding: 1.25rem 0;
    }
    @include rwd($rwd-lg){
        padding: 1.75rem 0;
    }
    &-item{
        width: 50%;
        padding: .5rem 0;
        text-align: center;
        border: 1px solid $white;
        &:first-child {
            padding-left: 0;
        }
        @include rwd($rwd-md){
            width: auto;
            padding: 0 2rem;
            border: none;
        }
    } 
    &-link{
        color: $white;
        &:hover{
            color: darken($secondary, 10%);
        }
    }   
}
.nav{
    border: 1px solid $gray-light;
    border-radius: .25rem;
    &-item{
        &:hover{
            color: $white;
            background: lighten($secondary, 10%);
            border-radius: none;
        }
        &:first-child {
            border-radius: .25rem .25rem 0 0;
        }
        &:last-child {
            border-radius: 0 0 .25rem .25rem;
        }
    }
    &-link{
        padding: .5rem .25rem;
        color: $gray-dark;
        &:hover{
            color: $white;
        }
    }
}