.section{
    padding: 2rem 0;
    @include rwd($rwd-md){
        padding: 2.5rem 0;
    }
    @include rwd($rwd-lg){
        padding: 5rem 0;
    }
    &-title{
        color: $secondary;
        font-size: $font-base-size * 1.5;
        font-weight: bold;
        text-align: center;
        margin-bottom: 1rem;
        @include rwd($rwd-md){
            font-size: $font-base-size * 2;
            margin-bottom: 2rem;
        }
        @include rwd($rwd-lg){
            font-size: $font-base-size * 3;
            margin-bottom: 4rem;
        }
    }
}